.App {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.map {
    z-index: 2;
    width: 100vw;
    height: 100vh;
}

.geoLocateButton {
    position: absolute;
    z-index: 3;
    top: 10px;
    left: 50px;
    display: -ms-flexbox;
    display: flex;
    width: 260px;
    height: 60px;
    background: rgba(100, 100, 100, .8);
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.geoLocateButton input {
    font-size: 16px;
    padding: 10px 50px;
    cursor: pointer;
    color: white;
    border: none;
    background: rgb(230, 60, 50);
}

.togglePanelButton {
	position: absolute;
	z-index: 3;
	width: 250px;
	height: 40px;
	left: 50px;
	top: 80px;
	cursor: pointer;
	color: white;
    border: none;
    background: rgb(90, 90, 90);
    font-size: 1em;
}

.filterPanel {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
/*	justify-content: center;
*/	-ms-flex-align: center;	align-items: center;
    position: absolute;
    z-index: 3;
    bottom: 15px;
    left: 10px;
    width: 100%;
    max-width: 300px;
    height: auto;
    background: rgba(100, 100, 100, .85);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    overflow-y: scroll;
}

.filterPanel input {
	white-space: normal;
    font-size: 14px;
    display: block;
    width: 40%;
    height: 50px;
    margin: 10px;
    cursor: pointer;
    color: white;
    border: none;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.blueButton {
	background: rgb(20, 150, 250);
}

.redButton {
	background: rgb(204, 45, 50);
}

.purpleButton {
	background: rgb(160, 60, 140);
}

.greenButton {
	background: rgb(20, 180, 70);
}

.closePanel {
	top: 200%;
}

.filterPanel .selected {
	margin-left: 25px;
	background: rgb(20, 190, 250);
	border: 4px solid rgb(50, 250, 250);
}

.numberOfShelters {
    font-size: 1.0em;
    margin: 40px;
    color: white;
    text-align: center;
}

.addAShelter {
	position: absolute;
	z-index: 2;
    bottom: 40px;
    right: 10px;
}

.addAShelter a {
	text-decoration: none;
	color: #ecf0f1;;
	padding: 15px 30px;
	background: #8e44ad;
	font-size: 1em;
	border-radius: .3rem;
}

input {
	border-radius: .3rem;
}

@media (min-width: 600px) {
	.geoLocateButton {
		top: 10px;
		right: 20px;
		left: auto;
	}
	.togglePanelButton {
		top: 10px;
	}

	.filterPanel {
	    top: 85px;
	    bottom: auto;
	    height: 85vh;
	}
	.filterPanel input {
		font-size: 16px;
	    width: 250px;
	    height: 50px;
	}
	.numberOfShelters {
	    font-size: 1.3em;
	    margin: 20px;
	}
	.closePanel {
		top: 200%;
	}

}


